.right-panel-container {
  bottom: 24px;
  position: fixed;
  right: 0;
  display: flex;
  -webkit-box-align: stretch;
  transition: margin 250ms;
}

.right-side-panel {
  background-color: #242730;
  text-align: center;
  max-width: 30%;
  /* max-width: 350px; defined in the component */
  /* max-height: 350px; again defined in the component */
}

.side-panel-container {
  top: 40px;
  height: 95%;
  display: flex;
  -webkit-box-align: stretch;
  transition: margin 250ms cubic-bezier(0, 0, 0.5, 0.1);
  position: absolute;
  padding-top: 20px;
  padding-bottom: 30px;
  padding-left: 20px;
}

.side-panel {
  padding: 20px;
  width: 300px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.side-pane-header {
  padding: 10px;
  margin-bottom: 20px;
  /* background-color: #29323C; */
}

.side-panel-body {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* border: 1px solid white; */
}

.close-button {
  transition: left 250ms, right 250ms;
}

.side-panel .dropdown-menu {
  border: 1px solid #adadad;
}

.space {
  flex:1 1;
}

.search-form {
  margin: auto;
  padding-bottom: 0;
  width: 100%;
  max-width: 320px;
}

.search-form .form-group {
  margin-bottom: 0;
}

.truncate {
  width: 250px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
